.App {
  text-align: center;
  font-family: "Trebuchet MS", Helvetica, sans-serif
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(20px + 2vmin);
  color: grey;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Dropdown {
  padding-top: 30px;
  padding-bottom: 30px;
}

.Feed {
  color:black;
  font-size:15px;
  width: 80%;
}

#Bold {
  font-weight: bold;
}